export const tasks_create = async (id: string, title: string, preview: string, rating: number, comments: object, priority: number): Promise<any> => {
  const req = {
    id: id,
    title: title,
    preview: preview,
    rating: rating,
    comments: comments,
    priority: priority
  };

  return new Promise((resolve, reject) => {
    $.ajax({
      url: `${SERVER}/tasks-create`,
      method: 'POST',
      dataType: 'json',
      data: req,
      success: (response: any) => resolve(response),
      error: (error: any) => reject(error)
    });
  });
};
