import React from 'react';
import $ from 'jquery'
import { Link } from 'react-router-dom';

export class VideosRateSuccesful extends React.Component {
  componentDidMount(): void {
    $('.videos-rate-succesful-content-submit').click(() => {
      $('.videos-rate-succesful').removeClass('shown');
    });
  };
  render() {
    return (
      <div className="videos-rate-succesful window">
        <div className="videos-rate-succesful-background window-background">

        </div>
        <div className="videos-rate-succesful-content window-content">
          <div className="videos-rate-succesful-content-icon">
            <i className="fa-solid fa-square-check"></i>
          </div>
          <div className="videos-rate-succesful-content-title">
             
          </div>
          <button className="videos-rate-succesful-content-submit">
            
          </button>
          <Link to='/tasks'>
            <button className="videos-rate-succesful-content-see">
                
            </button>
          </Link>
        </div>
      </div>
    );
  };
};