import React, { createContext, useState, useContext, ReactNode, useEffect } from 'react';

interface AuthContextType {
  isAuthenticated: boolean;
  login: string | null;
  password: string | null;
  userName: string | null;
  maxAccounts: number | null;
  isAdmin: boolean;
  authenticate: (login: string, password: string, userName: string, maxAccounts: number, isAdmin: boolean) => void;
  logout: () => void;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(() => {
    const savedAuthState = localStorage.getItem('isAuthenticated');
    return savedAuthState ? JSON.parse(savedAuthState) : false;
  });

  const [userName, setUserName] = useState<string | null>(() => {
    return localStorage.getItem('userName');
  });

  const [login, setLogin] = useState<string | null>(() => {
    return localStorage.getItem('login');
  });

  const [password, setPassword] = useState<string | null>(() => {
    return localStorage.getItem('password');
  });

  const [maxAccounts, setMaxAccounts] = useState<number | null>(() => {
    const savedMaxAccounts = localStorage.getItem('maxAccounts');
    return savedMaxAccounts ? JSON.parse(savedMaxAccounts) : null;
  });

  const [isAdmin, setIsAdmin] = useState<boolean>(() => {
    const savedIsAdmin = localStorage.getItem('isAdmin');
    return savedIsAdmin ? JSON.parse(savedIsAdmin) : false;
  });

  const authenticate = (login: string, password: string, userName: string, maxAccounts: number, isAdmin: boolean) => {
    setIsAuthenticated(true);
    setUserName(userName);
    setLogin(login);
    setPassword(password);
    setMaxAccounts(maxAccounts);
    setIsAdmin(isAdmin);
    localStorage.setItem('isAuthenticated', JSON.stringify(true));
    localStorage.setItem('userName', userName);
    localStorage.setItem('login', login);
    localStorage.setItem('password', password);
    localStorage.setItem('maxAccounts', JSON.stringify(maxAccounts));
    localStorage.setItem('isAdmin', JSON.stringify(isAdmin));
  };

  const logout = () => {
    setIsAuthenticated(false);
    setUserName(null);
    setLogin(null);
    setPassword(null);
    setMaxAccounts(null);
    setIsAdmin(false);
    localStorage.removeItem('isAuthenticated');
    localStorage.removeItem('userName');
    localStorage.removeItem('login');
    localStorage.removeItem('password');
    localStorage.removeItem('maxAccounts');
    localStorage.removeItem('isAdmin');
  };

  useEffect(() => {
    const savedAuthState = localStorage.getItem('isAuthenticated');
    const savedUserName = localStorage.getItem('userName');
    const savedLogin = localStorage.getItem('login');
    const savedPassword = localStorage.getItem('password');
    const savedMaxAccounts = localStorage.getItem('maxAccounts');
    const savedIsAdmin = localStorage.getItem('isAdmin');
    if (savedAuthState) {
      setIsAuthenticated(JSON.parse(savedAuthState));
    }
    if (savedUserName) {
      setUserName(savedUserName);
    }
    if (savedLogin) {
      setLogin(savedLogin);
    }
    if (savedPassword) {
      setPassword(savedPassword);
    }
    if (savedMaxAccounts) {
      setMaxAccounts(JSON.parse(savedMaxAccounts));
    }
    if (savedIsAdmin) {
      setIsAdmin(JSON.parse(savedIsAdmin));
    }
  }, []);

  return (
    <AuthContext.Provider value={{ isAuthenticated, userName, login, password, maxAccounts, isAdmin, authenticate, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};