import React from 'react';
import { Link } from 'react-router-dom';

interface MenuProps {
  data: {
    page?: string;
    isBig?: boolean;
  }
};

export class Menu extends React.Component<MenuProps> {
  componentDidMount(): void {
    const { data: { page } } = this.props;

    setTimeout(() => { $(`.menu-element[href="${page}"]`).addClass('active'); }, 100);

    $('.menu-bars').click(() => {
      $('#menu').toggleClass('big');
    });

    if (this.props.data.isBig) {
      $('#menu').addClass('big');
    };
  };
  render() {
    return (
      <div className="menu" id="menu">
        <div className="menu-bars">
          <i className="fa-solid fa-bars"></i>
        </div>
        <div className="menu-container">
          <Link to='/' className='menu-element'>
            <i className="fa-solid fa-house"></i>&nbsp;
            <span className='menu-element-name'>
              
            </span>
          </Link>
          <Link to='/tasks' className="menu-element">
            <i className="fa-solid fa-fire"></i>&nbsp;
            <span className='menu-element-name'>
              
            </span>
          </Link>
          <Link to='/accounts' className="menu-element">
            <i className="fa-solid fa-users"></i>&nbsp;
            <span className='menu-element-name'>
              
            </span>
          </Link>
        </div>
      </div>
    );
  };
};