import React, { useEffect } from 'react';
import $ from 'jquery';
import { useAuth } from '../../AuthContext';
import { get_users } from '../../services';

export const Authorize: React.FC = () => {
  const auth = useAuth();

  useEffect(() => {
    $('.authorize-background').click(() => {
      $('.authorize').removeClass('shown');
    });

    $('.authorize-content-button').click(() => {
      const login: any = $('.authorize-content-login').val();
      const password: any = $('.authorize-content-password').val();

      if (login !== '' && password !== '') {
        get_users().then(response => {
          for (let i = 0; i < response.length; i++) {
            if (response[i].login === login) {
              if (response[i].password === password) {
                auth.authenticate(login, password, response[i].username, response[i].maxAccounts, response[i].isAdmin);

                $('.authorize').removeClass('shown');
                $('.header-unauthorized').hide();
                $('.header-authorized').show();
                $('.header-user-name').text(response[i].username);
                return;
              }
            }
          }
          alert('   ');
        }).catch((error) => {
          console.log(error);
        });
      } else {
        alert(',   ');
      }
    });
  }, [auth]);

  return (
    <div className="authorize window">
      <div className="authorize-background window-background"></div>
      <div className="authorize-content window-content">
        <div className="section">
          <div className="section-content">
            <input className="authorize-content-login" type="text" placeholder='' />
            <input className="authorize-content-password" type="password" placeholder='' />
            <button className="authorize-content-button"></button>
          </div>
        </div>
      </div>
    </div>
  );
};