import React from "react";
import $ from "jquery";
import { Menu } from "../parts/Menu";
import { get_accounts } from "../../services";

interface AccountsProps {
  data: {
    self: boolean;
  };
};

export class Accounts extends React.Component<AccountsProps> {
  componentDidMount(): void {
    get_accounts().then(response => {
      response.forEach((account: any) => {
        $('.accounts-container').append(
          `<div class="account">
            <div class="account-login">
              ${account.login} &nbsp;
              <i class="account-login-copy fa-regular fa-copy"></i>
            </div>
            <div class="account-password">
              ${account.password} &nbsp;
              <i class="account-password-copy fa-regular fa-copy"></i>
            </div>
            <div class="account-date">
              ${account.date.slice(0, 10)}
            </div>
          </div>`
        );

        $('.account-login-copy').last().click(() => {
          navigator.clipboard.writeText(account.login);
        });
        $('.account-password-copy').last().click(() => {
          navigator.clipboard.writeText(account.password);
        });
      });
    }).catch(error => {
      console.log(error);
    });
  };
  render() {
    return (
      <React.Fragment>
        {this.props.data.self ? <Menu data={{ page: '/accounts', isBig: $('.menu').is('.big') }} /> : null}
        <div className="content accounts" id="content">
          <div className="content-background">
            
          </div>
          <div className="accounts-container">

          </div>
        </div>
      </React.Fragment>
    );
  };
}