import React from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';
import sal from 'sal.js';
import 'sal.js/dist/sal.css';
import { VideosRate } from '../windows/VideosRate'
import { ShortsRate } from '../windows/ShortsRate';
import { BroadcastsRate } from '../windows/BroadcastsRate';
import { Menu } from '../parts/Menu';
import { Pie } from 'react-chartjs-2';
import { Chart, ArcElement } from 'chart.js';
import { accounts_registration_disable, accounts_registration_enable, accounts_registration, check_database_status, check_server_status, check_servers_load, get_accounts, get_tasks } from '../../services';

Chart.register(ArcElement);

interface HomeProps {
  data: {
    self: boolean;
    maxAccounts: number | null;
    isAdmin: boolean;
  };
};

interface HomeState {
  accounts: {
    free: number;
    busy: number;
  };
  accounts_pie_data: {
    datasets: {
      data: number[];
      borderWidth: number;
      borderColor: string[];
      backgroundColor: string[];
    }[];
  };
  tasks: {
    prioritySum: number;
  };
  tasks_pie_data: {
    datasets: {
      data: number[];
      borderWidth: number;
      borderColor: string[];
      backgroundColor: string[];
    }[];
  };
  load: {
    prioritySum: number;
  };
  load_pie_data: {
    datasets: {
      data: number[];
      borderWidth: number;
      borderColor: string[];
      backgroundColor: string[];
    }[];
  };
}

export class Home extends React.Component<HomeProps, HomeState> {
  constructor(props: HomeProps) {
    super(props);
    this.state = {
      accounts: {
        free: 0,
        busy: 0
      },
      accounts_pie_data: {
        datasets: [
          {
            data: [0, 0],
            borderWidth: 0,
            borderColor: ['rgba(0, 0, 0, 0)', 'rgba(0, 0, 0, 0)'],
            backgroundColor: ['rgba(200, 100, 40, 1)', 'rgba(250, 250, 250, 0.05)']
          },
        ],
      },
      tasks: {
        prioritySum: 0
      },
      tasks_pie_data: {
        datasets: [
          {
            data: [0, 0],
            borderWidth: 0,
            borderColor: ['rgba(0, 0, 0, 0)', 'rgba(0, 0, 0, 0)'],
            backgroundColor: ['rgba(200, 100, 40, 1)', 'rgba(250, 250, 250, 0.05)']
          },
        ],
      },
      load: {
        prioritySum: 0
      },
      load_pie_data: {
        datasets: [
          {
            data: [0, 0],
            borderWidth: 0,
            borderColor: ['rgba(0, 0, 0, 0)', 'rgba(0, 0, 0, 0)'],
            backgroundColor: ['rgba(200, 100, 40, 1)', 'rgba(250, 250, 250, 0.05)']
          },
        ],
      }
    };
  };
  componentDidMount(): void {
    accounts_registration().then((response) => {
      if (response.status === 'enabled') {
        $('#home-registration-status-switcher').find('.switcher').addClass('active');
        $('#home-registration-status-switcher').find('.switcher-circle').addClass('active');
      } else if (response.status === 'disabled') {
        $('#home-registration-status-switcher').find('.switcher').removeClass('active');
        $('#home-registration-status-switcher').find('.switcher-circle').removeClass('active');
      }
    }).catch((error) => {
      console.log(error);
    });

    $('#home-registration-status-switcher').click(() => {
      if (!$('#home-registration-status-switcher').find('.switcher').is('.active')) {
        accounts_registration_enable().then((response) => {
          if (response.status === 'successful') {
            $('#home-registration-status-switcher').find('.switcher').addClass('active');
            $('#home-registration-status-switcher').find('.switcher-circle').addClass('active');
          }
        }).catch((error) => {
          console.log(error);
        });
      } else {
        accounts_registration_disable().then((response) => {
          if (response.status === 'successful') {
            $('#home-registration-status-switcher').find('.switcher').removeClass('active');
            $('#home-registration-status-switcher').find('.switcher-circle').removeClass('active');
          }
        }).catch((error) => {
          console.log(error);
        });
      };
    });

    $('#see-videos').click(() => { $('.videos').addClass('shown') });

    $('#see-shorts').click(() => { $('.shorts').addClass('shown') });

    $('#see-broadcasts').click(() => { $('.broadcasts').addClass('shown') });

    sal();

    const updateAccounts = () => {
      get_accounts().then((response) => {
        const accounts = response;
        const free = accounts.filter((account: any) => account.status === 'free').length;
        const busy = accounts.filter((account: any) => account.status === 'busy').length;

        const total = free + busy;
        const freePercent = (free / total) * 100;
        const busyPercent = (busy / total) * 100;

        this.setState(prevState => ({
          accounts: {
            free: free,
            busy: busy
          },
          accounts_pie_data: {
            ...prevState.accounts_pie_data,
            datasets: prevState.accounts_pie_data.datasets.map(dataset => ({
              ...dataset,
              data: [busyPercent, freePercent]
            }))
          }
        }));

        $('#about-accounts-free').text(free);
        $('#about-accounts-busy').text(busy);

        const today = new Date().toISOString().split('T')[0];
        const todayRegistrations = accounts.filter((account: any) => account.date && account.date.startsWith(today)).length;
        
        $('#home-registration-lastday').text(todayRegistrations);
      }).catch((error) => {
        console.log(error);
      });
    };

    const updateTasks = () => {
      get_tasks().then((response) => {
        const tasks = response;
        let prioritySum = 0;

        for (let i = 0; i < tasks.length; i++) { prioritySum += tasks[i].priority };

        this.setState(prevState => ({
          tasks: {
            prioritySum: prioritySum
          },
          tasks_pie_data: {
            ...prevState.tasks_pie_data,
            datasets: prevState.tasks_pie_data.datasets.map(dataset => ({
              ...dataset,
              data: [prioritySum, 100 - prioritySum]
            }))
          }
        }));

        $('#about-tasks-priority-available').text(`${100 - prioritySum}%`);
        $('#about-tasks-priority-used').text(`${prioritySum}%`);
      }).catch((error) => {
        console.log(error);
      });
    };

    const updateServersLoad = () => {
      check_servers_load().then((response) => {
        let loadSum = 0;

        for (let i = 0; i < 1; i++) { 
          const total = response.totalMemory;
          const free = response.freeMemory;
          const used = response.usedMemory;

          loadSum += (used / total) * 100;

          this.setState(prevState => ({
            load: {
              prioritySum: Math.round(loadSum)
            },
            load_pie_data: {
              ...prevState.load_pie_data,
              datasets: prevState.load_pie_data.datasets.map(dataset => ({
                ...dataset,
                data: [loadSum, 100 - loadSum]
              })
              )
            }
          }));
        };
      }).catch((error) => {
        console.log(error);
      });
    };

    const updateServerStatus = () => {
      if (!$('#server-status').is('.working')) {
        $('#server-status').addClass('sleeping');
      };

      check_server_status().then((response) => {
        $('#server-status').removeClass('sleeping');
        $('#server-status').addClass('working');
      }).catch((error) => {
        console.log(error);
      });
    };

    const updateDatabaseStatus = () => {
      if (!$('#database-status').is('.working')) {
        $('#database-status').addClass('sleeping');
      };

      check_database_status().then((response) => {
        $('#database-status').removeClass('sleeping');
        $('#database-status').addClass('working');
      }).catch((error) => {
        console.log(error);
      });
    };

    // Initial data fetch
    updateAccounts();
    updateTasks();
    updateServersLoad();
    updateServerStatus();
    updateDatabaseStatus();

    // Set intervals to update data periodically
    setInterval(updateAccounts, 1000);
    setInterval(updateTasks, 1000);
    setInterval(updateServersLoad, 1000);
    setInterval(updateServerStatus, 1000);
    setInterval(updateDatabaseStatus, 1000);
  }

  render() {
    const options = {
      responsive: true,
      maintainAspectRatio: false,
    };

    return (
      <React.Fragment>
        <VideosRate />
        <ShortsRate />
        <BroadcastsRate />
        {this.props.data.self ? <Menu data={{ page: '/', isBig: $('.menu').is('.big') }} /> : null}
        <div className="content home" id="content">
          <div className="content-background">

          </div>
          <div className="content-title"
            data-sal="slide-up"
            data-sal-delay="300"
            data-sal-duration="700"
            data-sal-easing="ease-out-back">
            YouTube
            <span style={{ color: 'rgba(200, 100, 40, 1)', fontWeight: '900' }}>
              <i className="fa-solid fa-rocket" style={{ transform: 'scale(80%)' }}></i>Boost
            </span>
          </div>
          <div className="home-container">
            <div className="home-element"
              data-sal="slide-up"
              data-sal-delay="650"
              data-sal-duration="800"
              data-sal-easing="ease-out-back">
              <div className="home-element-title">
                
              </div>
              <div className="home-element-content">
                <div className="home-element-content-accounts-container">
                  <div className="home-element-content-accounts-diagram-container">
                    <div className="home-element-content-accounts-diagram">
                      <Pie data={this.state.accounts_pie_data} options={options} />
                    </div>
                    <div className="home-element-content-accounts-diagram-description-container">
                      <div className='home-element-content-accounts-diagram-description'>
                        <div>
                          
                        </div>
                        <div id='home-accounts-free' style={{ color: 'rgba(200, 100, 40, 1)' }}>
                          {this.state.accounts.free}
                        </div>
                      </div>
                      <div className='home-element-content-accounts-diagram-description'>
                        <div>
                          
                        </div>
                        <div id='home-accounts-busy' style={{ color: 'rgba(200, 100, 40, 1)' }}>
                          {this.state.accounts.busy}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="home-element"
              data-sal="slide-up"
              data-sal-delay="700"
              data-sal-duration="800"
              data-sal-easing="ease-out-back">
              <div className="home-element-title">
                 
              </div>
              <div className="home-element-content">
                <div className="home-element-content-tasks-container">
                  <div className="home-element-content-tasks-diagram-container">
                    <div className="home-element-content-tasks-diagram">
                      <Pie data={this.state.tasks_pie_data} options={options} />
                    </div>
                    <div className="home-element-content-tasks-diagram-description-container">
                      <div className='home-element-content-tasks-diagram-description'>
                        <div>
                          
                        </div>
                        <div id='home-tasks-priority-available' style={{ color: 'rgba(200, 100, 40, 1)' }}>
                          {100 - this.state.tasks.prioritySum}%
                        </div>
                      </div>
                      <div className='home-element-content-tasks-diagram-description'>
                        <div>
                          
                        </div>
                        <div id='home-tasks-priority-used' style={{ color: 'rgba(200, 100, 40, 1)' }}>
                          {this.state.tasks.prioritySum}%
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="home-element"
              data-sal="slide-up"
              data-sal-delay="750"
              data-sal-duration="800"
              data-sal-easing="ease-out-back">
              <div className="home-element-title">
                RAM
              </div>
              <div className="home-element-content">
                <div className="home-element-content-load-container">
                  <div className="home-element-content-load-diagram-container">
                    <div className="home-element-content-load-diagram">
                      <Pie data={this.state.load_pie_data} options={options} />
                    </div>
                    <div className="home-element-content-load-diagram-description-container">
                      <div className='home-element-content-load-diagram-description'>
                        <div>
                          
                        </div>
                        <div id='home-load-priority-available' style={{ color: 'rgba(200, 100, 40, 1)' }}>
                          {100 - this.state.load.prioritySum}%
                        </div>
                      </div>
                      <div className='home-element-content-load-diagram-description'>
                        <div>
                          
                        </div>
                        <div id='home-load-priority-used' style={{ color: 'rgba(200, 100, 40, 1)' }}>
                          {this.state.load.prioritySum}%
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="home-element"
              data-sal="slide-up"
              data-sal-delay="800"
              data-sal-duration="800"
              data-sal-easing="ease-out-back">
              <div className="home-element-title">
                 
              </div>
              <div className="home-element-content">
                <div className="home-element-content-database-container">
                  <div className="home-element-content-database">
                    <i className="fa-solid fa-database"></i>
                    <div className="home-element-content-database-status" id='database-status'>

                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="home-element"
              data-sal="slide-up"
              data-sal-delay="850"
              data-sal-duration="800"
              data-sal-easing="ease-out-back">
              <div className="home-element-title">
                 
              </div>
              <div className="home-element-content">
                <div className="home-element-content-server-container">
                  <div className="home-element-content-server">
                    <i className="fa-solid fa-server"></i>
                    <div className="home-element-content-server-status" id='server-status'>

                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="home-element"
              data-sal="slide-up"
              data-sal-delay="900"
              data-sal-duration="800"
              data-sal-easing="ease-out-back">
              <div className="home-element-title">
                 
              </div>
              <div className="home-element-content">
                <div className="home-element-content-server_helpers-container">
                  <div className="home-element-content-server_helpers">
                    <i className="fa-solid fa-server"></i>
                    <div className="home-element-content-server_helpers-status" id='server_helpers-status'>

                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="home-element"
              data-sal="slide-up"
              data-sal-delay="950"
              data-sal-duration="800"
              data-sal-easing="ease-out-back">
              <div className="home-element-title">
                
              </div>
              <div className="home-element-content">
                <div className="home-element-content-registration-container">
                  <div className="home-element-content-registration-lastday-container">
                    <div className="home-element-content-registration-lastday-name">
                       
                    </div>
                    <div className="home-element-content-registration-lastday-value" id='home-registration-lastday'>
                      0
                    </div>
                  </div>
                  <div className="home-element-content-registration-status-container">
                    <div className="home-element-content-registration-status-name">
                      
                    </div>
                    <div className="home-element-content-registration-status-switcher" id='home-registration-status-switcher'>
                      <div className="switcher">
                        <div className="switcher-circle">

                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="home-element-content-registration-accounts-container">
                    <div className="home-element-content-registration-accounts-name">
                      
                    </div>
                    <Link to='/accounts' className="home-element-content-registration-accounts-see">
                      <button>
                        <i className="fa-solid fa-angle-right"></i>
                      </button>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
            <div className="home-element"
              data-sal="slide-up"
              data-sal-delay="1000"
              data-sal-duration="800"
              data-sal-easing="ease-out-back">
              <div className="home-element-title">
                 
              </div>
              <div className="home-element-content">
                <div className="home-element-content-newtask-container">
                  <div className="home-element-content-newtask-element">
                    <div className="home-element-content-newtask-element-title">
                      
                    </div>
                    <div className="home-element-content-newtask-element-see">
                      <button id='see-videos'>
                        <i className="fa-solid fa-angle-right"></i>
                      </button>
                    </div>
                  </div>
                  <div className="home-element-content-newtask-element">
                    <div className="home-element-content-newtask-element-title">
                      
                    </div>
                    <div className="home-element-content-newtask-element-see">
                      <button id='see-shorts'>
                        <i className="fa-solid fa-angle-right"></i>
                      </button>
                    </div>
                  </div>
                  <div className="home-element-content-newtask-element">
                    <div className="home-element-content-newtask-element-title">
                      
                    </div>
                    <div className="home-element-content-newtask-element-see">
                      <button id='see-broadcasts'>
                        <i className="fa-solid fa-angle-right"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
};