import React from "react";
import $ from "jquery";

export class ShortsRate extends React.Component {
  componentDidMount(): void {
    $('.shorts').find('.window-background').on('click', () => {
      $('.shorts').removeClass('shown');
    });
  };
  render() {
    return (
      <React.Fragment>
        <div className="shorts window" id="content">
          <div className="window-background">
            
          </div>
          <div className="window-content">

          </div>
        </div>
      </React.Fragment>
    );
  };
}