import React from 'react';
import { Routes, Route } from 'react-router-dom';
import './App.css';

import { Header } from './components/parts/Header';
import { NotFound } from './components/pages/NotFound';
import { Home } from './components/pages/Home';
import { Accounts } from './components/pages/Accounts';
import { Tasks } from './components/pages/Tasks';
import { useAuth } from './AuthContext';

export const AuthorizedApp: React.FC = () => {
  const { userName, maxAccounts, isAdmin } = useAuth();

  return (
    <React.Fragment>
      <Header data={{ isAuthorized: true, userName }} />
      <Routes>
        {/* NotFound */}
        <Route path='*' element={<NotFound />} />

        {/* Home */}
        <Route path='/' element={<Home data={{ self: true, maxAccounts: maxAccounts, isAdmin: isAdmin }} />} />

        {/* Tasks */}
        <Route path='/tasks' element={<Tasks data={{ self: true }} />} />

        {/* Accounts */}
        <Route path='/accounts' element={<Accounts data={{ self: true }} />} />
      </Routes>
    </React.Fragment>
  );
};