import React from 'react';
import $ from 'jquery'

export class VideosRateConfirm extends React.Component {
  componentDidMount(): void {
    $('#videos-rate-confirm-content-actions-cancel').on('click', () => {
      $('.videos-rate-confirm').removeClass('shown');
    });
  };
  render() {
    return (
      <div className="videos-rate-confirm window" style={{ zIndex: 5 }}>
        <div className="videos-rate-confirm-background window-background">

        </div>
        <div className="videos-rate-confirm-content window-content">
          <div className="videos-rate-confirm-content-title">
             ?
          </div>
          <div className="videos-rate-confirm-content-title-container" style={{ marginTop: '21px' }}>
            <div className="videos-rate-confirm-content-title-name">
              <i className="fa-regular fa-circle-play"></i>&nbsp;
              
            </div>
            <div className="videos-rate-confirm-content-title-value" id="videos-rate-confirm-content-title">
              ?
            </div>
          </div>
          <div className="videos-rate-confirm-content-rating-container">
            <div className="videos-rate-confirm-content-rating-name">
              <i className="fa-regular fa-thumbs-up"></i>&nbsp;
              
            </div>
            <div className="videos-rate-confirm-content-rating-value" id="videos-rate-confirm-content-rating">
              ?
            </div>
          </div>
          <div className="videos-rate-confirm-content-comments-theme-container">
            <div className="videos-rate-confirm-content-comments-theme-name">
              <i className="fa-regular fa-comment-dots"></i>&nbsp;
              
            </div>
            <div className="videos-rate-confirm-content-comments-theme-value" id="videos-rate-confirm-content-comments-theme">
              ?
            </div>
          </div>
          <div className="videos-rate-confirm-content-comments-audience-container">
            <div className="videos-rate-confirm-content-comments-audience-name">
              <i className="fa-regular fa-comment-dots"></i>&nbsp;
              
            </div>
            <div className="videos-rate-confirm-content-comments-audience-value" id="videos-rate-confirm-content-comments-audience">
              ?
            </div>
          </div>
          <div className="videos-rate-confirm-content-priority-container">
            <div className="videos-rate-confirm-content-priority-name">
              <i className="fa-solid fa-chart-simple"></i>&nbsp;
              
            </div>
            <div className="videos-rate-confirm-content-priority-value" id="videos-rate-confirm-content-priority">
              ?
            </div>
          </div>
          <div className="videos-rate-confirm-content-actions" style={{ marginTop: '21px' }}>
            <button className="videos-rate-confirm-content-actions-cancel" id="videos-rate-confirm-content-actions-cancel">
              <i className="fa-solid fa-pen"></i>
            </button>
            <button className="videos-rate-confirm-content-actions-submit" id="videos-rate-confirm-content-actions-submit">
              <i className="fa-solid fa-check"></i>
            </button>
          </div>
        </div>
      </div>
    );
  };
};