import React from "react";
import { get_tasks, tasks_update } from "../../services";
import { Menu } from "../parts/Menu";

interface TasksProps {
  data: {
    self: boolean;
  };
};

export class Tasks extends React.Component<TasksProps> {
  componentDidMount(): void {
    get_tasks().then((response) => {
      let fixedPrioritySum: any = response.reduce((sum: number, task: { priority: number }) => sum + task.priority, 0);
      
      for (let i = 0; i < response.length; i++) {
        $('.tasks-container').append(`
          <div class="task">
            <div class="task-preview"></div>
            <div class="task-title"></div>
            <div class="task-priority-container" id="task-priority-container">
              <div class="task-priority-field" id="task-priority-field" data-value='0'>
                <div class="task-priority-field-active" id="task-priority-field-active"></div>
              </div>
              <input class="task-priority-value" id="task-priority-value" type="text" value="0" maxLength="3">
            </div>
          </div>
        `);

        let PriorityMax = (100 - fixedPrioritySum) + response[i].priority;

        const taskElement = $('.tasks-container').children().last();
        taskElement.find('.task-preview').css({ backgroundImage: response[i].video_preview });
        taskElement.find('.task-title').text(response[i].video_title);
        taskElement.find('#task-priority-field').attr('data-value', response[i].priority);
        taskElement.find('#task-priority-field-active').css('width', `${response[i].priority}%`);
        taskElement.find('#task-priority-value').val(response[i].priority);
        $('.tasks-priority-counter-used').text(`${fixedPrioritySum}%`);

        taskElement.find('#task-priority-value').on('keypress', function (e) {
          if (e.which === 13) {
            const newPriority = parseInt($(this).val() as string, 10);
            if (!isNaN(newPriority) && newPriority >= 0 && newPriority <= PriorityMax) {
              tasks_update(response[i].id, newPriority).then(() => {
                fixedPrioritySum += newPriority - response[i].priority;
                response[i].priority = newPriority;
                PriorityMax = (100 - fixedPrioritySum) + response[i].priority;
                taskElement.find('#task-priority-field').attr('data-value', newPriority);
                taskElement.find('#task-priority-field-active').css('width', `${newPriority}%`);
                taskElement.find('#task-priority-value').val(newPriority);
                $('.tasks-priority-counter-used').text(`${fixedPrioritySum}%`);
              }).catch((error) => {
                console.error(error);
              });
            } else {
              alert(`    0  ${PriorityMax}`);
            };
          };
        });
      };
    }).catch((error) => {
      console.error(error);
    });
  };

  render() {
    return (
      <React.Fragment>
        {this.props.data.self ? <Menu data={{ page: '/tasks', isBig: $('.menu').is('.big') }} /> : null}
        <div className="content tasks" id="content">
          <div className="content-background"></div>
          <div className="tasks-priority-counter-container">
            <i className="fa-solid fa-chart-simple"></i>
            <div className="tasks-priority-counter-used">
              ?
            </div>
          </div>
          <div className="tasks-container">

          </div>
        </div>
      </React.Fragment>
    );
  }
}