import React from 'react';
import $ from 'jquery'
import { Link } from 'react-router-dom';

export class Error extends React.Component {
  componentDidMount(): void {
    $('.error-content-submit').click(() => {
      $('.error').removeClass('shown');
    });
  };
  render() {
    return (
      <div className="error window">
        <div className="error-background window-background">

        </div>
        <div className="error-content window-content">
          <div className="error-content-icon">
            <i className="fa-solid fa-square-xmark"></i>
          </div>
          <div className="error-content-title">
            
          </div>
          <div className="error-content-description">
                 .<br />,  .
          </div>
          <button className="error-content-submit">
            
          </button>
        </div>
      </div>
    );
  };
};