import React, { useEffect } from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';
import { useAuth } from '../../AuthContext';
import { Authorize } from '../windows/Authorize';

interface HeaderProps {
  data: {
    isAuthorized: boolean;
    userName: string | null;
  };
};

export const Header: React.FC<HeaderProps> = ({ data }) => {
  const { logout } = useAuth();

  useEffect(() => {
    if (data.isAuthorized) {
      $('.header-unauthorized').hide();
    } else {
      $('.header-authorized').hide();
    }

    $('.header-authorize-button').on('click', () => {
      $('.authorize').addClass('shown');
    });

    $('.header-user-options-button').on('click', () => {
      $('.header-user-options-select').toggleClass('shown');
    });

    $('#logout').on('click', () => { logout() });

    return () => {
      $('.header-authorize-button').off('click');
      $('.header-user-options-button').off('click');
      $('#logout').off('click');
    };
  }, [data.isAuthorized, logout]);

  return (
    <React.Fragment>
      <Authorize />
      <div className="header" id="header">
        <div className="header-unauthorized">
          <button className='header-authorize-button'>
            
          </button>
        </div>
        <div className="header-authorized">
          <div className="header-user-name">
            {data.userName}
          </div>
          <div className="header-user-options-button">
            <i className="fa-regular fa-circle-user"></i>
          </div>
        </div>
      </div>
      <div className="header-user-options-select">
        <Link to="/profile" className='header-user-options-option'>
          <i className="fa-regular fa-user"></i>&nbsp;
          
        </Link>
        <div className='header-user-options-option' id='logout'>
          <i className="fa-solid fa-right-from-bracket"></i>&nbsp;
          
        </div>
      </div>
    </React.Fragment>
  );
};