import React from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
import { get_accounts, accounts_authentication, videos, get_tasks, tasks_create } from '../../services';
import { Error } from './Error';
import { VideosRateConfirm } from './VideosRateConfirm';
import { VideosRateSuccesful } from './VideosRateSuccesful';

export class VideosRate extends React.Component {
  componentDidMount(): void {
    let priority_max: number;

    get_accounts().then(response => {
      let free = response.filter((account: { status: string }) => account.status === 'free');
      let busy = response.filter((account: { status: string }) => account.status === 'busy');
    }).catch(error => {
      console.error('Error:', error);
    });

    get_tasks().then(response => {
      if (response.length > 0) {
        let priority_sum = 0;

        for (let i = 0; i < response.length; i++) {
          priority_sum += Number(response[i].priority);
          if (i === response.length - 1) {
            priority_max = Number(100 - priority_sum);
          };
        }
      } else {
        priority_max = Number(100);
      };

      $('#videos-rate-priority-field').attr('data-value', priority_max);
      $('#videos-rate-priority-field-active').css('width', `${priority_max}%`);
      $('#videos-rate-priority-point').css('left', `calc(${priority_max}% - 12px + 6px)`);
      $('#videos-rate-priority-value').text(`${priority_max}%`);
      $('#videos-rate-priority-max').text(`${priority_max}%`);
    }).catch(error => {
      console.error('Error:', error);
    });

    $('#accounts-authentication-submit').on('click', () => {
      accounts_authentication().then(response => {

      }).catch(error => {
        $('.error').addClass('shown');
        console.error('Error:', error);
      });
    });

    $('#videos-rate-id-submit').click(() => {
      const videoId = $('#videos-rate-id').val() as string;
      videos(videoId).then(response => {
        if (response.status === 'successful') {
          $('#videos-rate-preview-container').css('display', 'flex');
          $('#videos-rate-preview-image').css('background-image', `url(${response.previewImage})`);
          $('#videos-rate-preview-title').text(response.title);
        } else if (response.status === 'fail') {
          $('#videos-rate-preview-container').css('display', 'none');
        };
      }).catch(error => {
        $('.error').addClass('shown');
        console.error('Error:', error);
      });
    });

    $('#videos-rate-submit').on('click', () => {
      const id = $('#videos-rate-id').val() as string;
      const title = $('#videos-rate-preview-title').text() as string;
      const preview = $('#videos-rate-preview-image').css('background-image') as string;
      const rating = Math.round(Number($('#videos-rate-rating-field').attr('data-value')));
      const comments_theme = ($('#videos-rate-comments-theme').val() as string | undefined)?.trim() || '';
      const comments_audience = ($('#videos-rate-comments-audience').val() as string | undefined)?.trim() || '';
      const comments_information = ($('#videos-rate-comments-information').val() as string | undefined)?.trim() || '';
      const comments_wishes = ($('#videos-rate-comments-wishes').val() as string | undefined)?.trim() || '';
      const comments_examples = $('#videos-rate-comments-example-array').children().map((i, e) => $(e).children('.videos-rate-comments-example-array-element-message').text().trim()).get() as string[];
      const comments = { theme: comments_theme, audience: comments_audience, information: comments_information, wishes: comments_wishes, examples: comments_examples };
      const priority = Number($('#videos-rate-priority-value').text().split('%')[0]);

      if (id !== '' && rating !== 0 && comments_theme !== '' && comments_audience !== '' && comments_information !== '' && comments_wishes !== '' && comments_examples.length !== 0) {
        $('#videos-rate-status-container').children().css('display', 'none');
        $('#videos-rate-status-point').removeClass('successful');
        $('#videos-rate-status-point').removeClass('fail');

        $('.videos-rate-confirm').addClass('shown');
        $('#videos-rate-confirm-content-title').text(title);
        $('#videos-rate-confirm-content-rating').text(`${rating}%`);
        $('#videos-rate-confirm-content-comments-theme').text(comments_theme);
        $('#videos-rate-confirm-content-comments-audience').text(comments_audience);
        $('#videos-rate-confirm-content-priority').text(`${priority}%`);
        $('#videos-rate-confirm-content-actions-cancel').unbind('click');
        $('#videos-rate-confirm-content-actions-submit').unbind('click');
        $('#videos-rate-confirm-content-actions-cancel').on('click', () => { $('.videos-rate-confirm').removeClass('shown') });
        $('#videos-rate-confirm-content-actions-submit').on('click', () => {
          $('.videos-rate-confirm').removeClass('shown');
          tasks_create(id, title, preview, rating, comments, priority).then((response: any) => {
            if (response.status === 'successful') {
              $('.videos-rate-succesful').addClass('shown');
            } else if (response.status === 'fail') {
              $('.error').addClass('shown');
            };
          }).catch((error: any) => {
            $('.error').addClass('shown');
            console.error('Error:', error);
          });
        });
      } else {
        alert('   ');
      };
    });

    $('.videos').find('.window-background').on('click', () => {
      $('.videos').removeClass('shown');
    });

    $('#videos-rate-rating-field').mousedown((e) => {
      if (e.buttons === 1) {
        const updatePosition = (event: MouseEvent) => {
          const ratingField = $('#videos-rate-rating-field');
          const ratingFieldActive = $('#videos-rate-rating-field-active');
          const ratingPoint = $('#videos-rate-rating-point');
          if (!ratingField.length || !ratingFieldActive.length || !ratingPoint.length) return;

          let position = (event.pageX - ratingField.offset()!.left) / ratingField.width()! * 100;
          position = Math.max(0, Math.min(100, position));
          ratingFieldActive.css('width', `${position}%`);
          ratingPoint.css('left', `calc(${position}% - 12px + 6px)`);
          if (parseInt(ratingFieldActive.css('width')!) <= 0) {
            ratingFieldActive.css('width', '0');
          } else if (parseInt(ratingFieldActive.css('width')!) >= ratingField.width()!) {
            ratingFieldActive.css('width', '100%');
          }
          if (parseInt(ratingPoint.css('left')!) <= -6) {
            ratingPoint.css('left', '-6px');
          } else if (parseInt(ratingPoint.css('left')!) >= ratingField.width()! - 6) {
            ratingPoint.css('left', 'calc(100% - 12px + 6px)');
          }
          $('#videos-rate-rating-description-percent').text(`${Math.round(position)}%`);
          ratingField.attr('data-value', position);
        };

        updatePosition(e as unknown as MouseEvent);

        const mouseMoveHandler = (event: MouseEvent) => updatePosition(event);
        $(document).on('mousemove', (event) => mouseMoveHandler(event as unknown as MouseEvent));

        $(document).on('mouseup', () => {
          $(document).off('mousemove');
        });
      };
    });

    $('#videos-rate-rating-point').mousedown((e) => {
      if (e.buttons === 1) {
        const updatePosition = (event: MouseEvent) => {
          const ratingField = $('#videos-rate-rating-field');
          const ratingFieldActive = $('#videos-rate-rating-field-active');
          const ratingPoint = $('#videos-rate-rating-point');
          if (!ratingField.length || !ratingFieldActive.length || !ratingPoint.length) return;

          let position = (event.pageX - ratingField.offset()!.left) / ratingField.width()! * 100;
          position = Math.max(0, Math.min(100, position));
          ratingFieldActive.css('width', `${position}%`);
          ratingPoint.css('left', `calc(${position}% - 12px + 6px)`);
          if (parseInt(ratingFieldActive.css('width')!) <= 0) {
            ratingFieldActive.css('width', '0');
          } else if (parseInt(ratingFieldActive.css('width')!) >= ratingField.width()!) {
            ratingFieldActive.css('width', '100%');
          }
          if (parseInt(ratingPoint.css('left')!) <= -6) {
            ratingPoint.css('left', '-6px');
          } else if (parseInt(ratingPoint.css('left')!) >= ratingField.width()! - 6) {
            ratingPoint.css('left', 'calc(100% - 12px + 6px)');
          }
          $('#videos-rate-rating-description-percent').text(`${Math.round(position)}%`);
          ratingField.attr('data-value', position);
        };

        updatePosition(e as unknown as MouseEvent);

        const mouseMoveHandler = (event: MouseEvent) => updatePosition(event);
        $(document).on('mousemove', (event) => mouseMoveHandler(event as unknown as MouseEvent));

        $(document).on('mouseup', () => {
          $(document).off('mousemove');
        });
      };
    });

    $('#videos-rate-priority-field').mousedown((e) => {
      if (e.buttons === 1) {
        const updatePosition = (event: MouseEvent) => {
          const ratingField = $('#videos-rate-priority-field');
          const ratingFieldActive = $('#videos-rate-priority-field-active');
          const ratingPoint = $('#videos-rate-priority-point');
          const ratingValue = $('#videos-rate-priority-value');
          if (!ratingField.length || !ratingFieldActive.length || !ratingPoint.length) return;

          let position = (event.pageX - ratingField.offset()!.left) / ratingField.width()! * 100;
          position = Math.max(0, Math.min(priority_max, position));
          ratingFieldActive.css('width', `${position}%`);
          ratingPoint.css('left', `calc(${position}% - 12px + 6px)`);
          if (parseInt(ratingFieldActive.css('width')!) <= 0) {
            ratingFieldActive.css('width', '0');
          } else if (parseInt(ratingFieldActive.css('width')!) >= ratingField.width()!) {
            ratingFieldActive.css('width', '100%');
          }
          if (parseInt(ratingPoint.css('left')!) <= -6) {
            ratingPoint.css('left', '-6px');
          } else if (parseInt(ratingPoint.css('left')!) >= ratingField.width()! - 6) {
            ratingPoint.css('left', 'calc(100% - 12px)');
          }
          $('#videos-rate-priority-description-percent').text(`${Math.round(position)}%`);
          ratingField.attr('data-value', position);
          ratingValue.text(`${Math.round(position)}%`);
        };

        updatePosition(e as unknown as MouseEvent);

        const mouseMoveHandler = (event: MouseEvent) => updatePosition(event);
        $(document).on('mousemove', (event) => mouseMoveHandler(event as unknown as MouseEvent));

        $(document).on('mouseup', () => {
          $(document).off('mousemove');
        });
      };
    });

    $('#videos-rate-priority-point').mousedown((e) => {
      if (e.buttons === 1) {
        const updatePosition = (event: MouseEvent) => {
          const ratingField = $('#videos-rate-priority-field');
          const ratingFieldActive = $('#videos-rate-priority-field-active');
          const ratingPoint = $('#videos-rate-priority-point');
          const ratingValue = $('#videos-rate-priority-value');
          if (!ratingField.length || !ratingFieldActive.length || !ratingPoint.length) return;

          let position = (event.pageX - ratingField.offset()!.left) / ratingField.width()! * 100;
          position = Math.max(0, Math.min(priority_max, position));
          ratingFieldActive.css('width', `${position}%`);
          ratingPoint.css('left', `calc(${position}% - 12px + 6px)`);
          if (parseInt(ratingFieldActive.css('width')!) <= 0) {
            ratingFieldActive.css('width', '0');
          } else if (parseInt(ratingFieldActive.css('width')!) >= ratingField.width()!) {
            ratingFieldActive.css('width', '100%');
          }
          if (parseInt(ratingPoint.css('left')!) <= -6) {
            ratingPoint.css('left', '-6px');
          } else if (parseInt(ratingPoint.css('left')!) >= ratingField.width()! - 6) {
            ratingPoint.css('left', 'calc(100% - 12px + 6px)');
          }
          $('#videos-rate-priority-description-percent').text(`${Math.round(position)}%`);
          ratingField.attr('data-value', position);
          ratingValue.text(`${Math.round(position)}%`);
        };

        updatePosition(e as unknown as MouseEvent);

        const mouseMoveHandler = (event: MouseEvent) => updatePosition(event);
        $(document).on('mousemove', (event) => mouseMoveHandler(event as unknown as MouseEvent));

        $(document).on('mouseup', () => {
          $(document).off('mousemove');
        });
      };
    });

    $('#videos-rate-comments-example').keydown((e) => {
      if (e.key === 'Enter') {
        e.preventDefault();
        $('#videos-rate-comments-example-submit').click();
      };
    });

    $('#videos-rate-comments-example-submit').click(() => {
      if ($('#videos-rate-comments-example').val() !== '') {
        if ($('#videos-rate-comments-example-array').css('display') !== 'grid') {
          $('#videos-rate-comments-example-array').css('display', 'grid');
        };
        $('#videos-rate-comments-example-array').append(`
          <div class="videos-rate-comments-example-array-element" id="videos-rate-comments-example-array-element">
            <div class="videos-rate-comments-example-array-element-message" id="videos-rate-comments-example-array-element-message">
              ${$('#videos-rate-comments-example').val()}
            </div>
            <button class="videos-rate-comments-example-array-element-remove" id="videos-rate-comments-example-array-element-remove">
              <i class="fa-regular fa-trash-can"></i>
            </button>
          </div>
        `);
        $('#videos-rate-comments-example-array').children().last().children('.videos-rate-comments-example-array-element-remove').click((e) => {
          if ($(e.target).is('i')) {
            $(e.target).parent().parent().remove();
          } else if ($(e.target).is('button')) {
            $(e.target).parent().remove();
          };
          if ($('#videos-rate-comments-example-array').children().length === 0) {
            $('#videos-rate-comments-example-array').css('display', 'none');
          };
        });
        $('#videos-rate-comments-example').val('');
        $('#videos-rate-comments-example').focus();
      };
    });
  };
  render() {
    return (
      <React.Fragment>
        <VideosRateConfirm />
        <VideosRateSuccesful />
        <Error />
        <div className="videos window">
          <div className="window-background">

          </div>
          <div className="window-content">
            <div className="section" id="section-rate">
              <div className="section-content">
                <h3>
                  <i className="fa-regular fa-circle-play"></i>&nbsp;
                  
                </h3>
                <div className="videos-rate-id-container">
                  <input className="videos-rate-id" id="videos-rate-id" type="text" placeholder="jNQXAC9IVRw"></input>
                  <button className="videos-rate-id-submit" id="videos-rate-id-submit">
                    <i className="fa-regular fa-eye"></i>
                  </button>
                </div>
                <div className="videos-rate-preview-container" id="videos-rate-preview-container">
                  <div className="videos-rate-preview-image" id="videos-rate-preview-image"></div>
                  <div className="videos-rate-preview-title" id="videos-rate-preview-title"></div>
                </div>
                <h3 style={{ marginTop: '21px' }}>
                  <i className="fa-regular fa-thumbs-up"></i>&nbsp;
                  
                </h3>
                <div className="videos-rate-rating-container" id="videos-rate-rating-container">
                  <div className="videos-rate-rating-field" id="videos-rate-rating-field" data-value="80">
                    <div className="videos-rate-rating-field-active" id="videos-rate-rating-field-active">

                    </div>
                  </div>
                  <div className="videos-rate-rating-point" id="videos-rate-rating-point">

                  </div>
                </div>
                <div className="videos-rate-rating-description" id="videos-rate-rating-description">
                     .  ,    ,    ,      <span id="videos-rate-rating-description-percent" style={{ color: 'rgba(200, 100, 40, 1)' }}>80%</span>  .
                </div>
                <h3 style={{ marginTop: '21px' }}>
                  <i className="fa-regular fa-comment-dots"></i>&nbsp;
                   
                </h3>
                <input className="videos-rate-comments-theme" id="videos-rate-comments-theme" type="text" placeholder="   Elden Ring"></input>
                <div className="videos-rate-comments-theme-description">
                   
                </div>
                <input className="videos-rate-comments-audience" id="videos-rate-comments-audience" type="text" placeholder="   13  17 "></input>
                <div className="videos-rate-comments-audience-description">
                  ,    
                </div>
                <textarea className="videos-rate-comments-information" id="videos-rate-comments-information" rows={6} placeholder="    Arthas     Elden Ring   RPG.           ..."></textarea>
                <div className="videos-rate-comments-information-description">
                     
                </div>
                <textarea className="videos-rate-comments-wishes" id="videos-rate-comments-wishes" rows={6} placeholder="       ,   "></textarea>
                <div className="videos-rate-comments-wishes-description">
                    
                </div>
                <h3 style={{ marginTop: '21px' }}>
                  <i className="fa-regular fa-comments"></i>&nbsp;
                   
                </h3>
                <div className="videos-rate-comments-example-container">
                  <input className="videos-rate-comments-example" id="videos-rate-comments-example" type="text" placeholder=""></input>
                  <button className="videos-rate-comments-example-submit" id="videos-rate-comments-example-submit">
                    <i className="fa-solid fa-plus"></i>
                  </button>
                </div>
                <div className="videos-rate-comments-example-array" id="videos-rate-comments-example-array">

                </div>
                <h3 style={{ marginTop: '21px' }}>
                  <i className="fa-solid fa-chart-simple"></i>&nbsp;
                  
                </h3>
                <div className="videos-rate-priority-container" id="videos-rate-priority-container">
                  <div className="videos-rate-priority-field" id="videos-rate-priority-field" data-value="0">
                    <div className="videos-rate-priority-field-active" id="videos-rate-priority-field-active">

                    </div>
                  </div>
                  <div className="videos-rate-priority-point" id="videos-rate-priority-point">

                  </div>
                </div>
                <div className="videos-rate-priority-description" id="videos-rate-priority-description">
                     - <span id="videos-rate-priority-value" style={{ color: 'rgba(200, 100, 40, 1)' }}>?</span>.    ,       .       - <span id="videos-rate-priority-max" style={{ color: 'rgba(200, 100, 40, 1)' }}>?</span>.
                </div>
                <div className="videos-rate-status-container" id="videos-rate-status-container">
                  <div className="videos-rate-status-point" id="videos-rate-status-point">

                  </div>
                  <div className="videos-rate-status-text" id="videos-rate-status-text">

                  </div>
                  <div className="videos-rate-status-quantity">
                    <span id="videos-rate-status-quantity">?</span> / <span id="videos-rate-status-quantity-total">?</span>
                  </div>
                </div>
                <button className="videos-rate-submit" id="videos-rate-submit">
                  <i className="fa-solid fa-rocket"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
};